.burger {
  display: flex;
  position: absolute;
  right: 12%;
  top: 50px;
  cursor: pointer;
  opacity: 0;
  background-color: transparent;
  border: none;
}

.burger div {
  position: relative;
  right: 0px;
  width: 25px;
  height: 2px;
  background-color: rgb(37, 37, 37);
  margin: 5px;
  transition: all 0.3s ease;
}

/*This gets rid of the menu background when the screen is bigger*/
.background {
  opacity: 0;
  width: 0px;
}

/*If the screen is smaller than 600 px it switches to small mode*/
@media screen and (max-width: 600px) {
  a {
    font-size: 28px;
    color: rgb(255, 255, 255);
  }

  a:hover {
    color: rgb(206, 206, 206);
    border-bottom: none;
  }

  a:active {
    color: rgb(128, 128, 128);
    border-bottom: none;
  }

  a:focus {
    color: rgb(255, 255, 255);
    border-bottom: none;
  }
  body {
    overflow-x: hidden;
    background-size: 330%, 100%;
    background-position: top, right;
  }

  .background {
    z-index: 5;
    opacity: 1;
    position: absolute;
    display: flexbox;
    background-color: rgba(29, 29, 29, 0.671);
    right: 0px;
    top: 0px;
    bottom: 0px;
    width: 0%;
  }
  .backgroundActive {
    z-index: 5;
    opacity: 1;
    position: absolute;
    background-color: rgba(29, 29, 29, 0.671);
    right: 0px;
    top: 0px;
    bottom: 0px;
    width: 35%;
  }

  .homeButton {
    z-index: 200;
    position: absolute;
    right: 10px;
    text-align: center;
    font-size: 30px;
    height: 47px;
    top: 15vh;
    width: 60%;
    z-index: -2000;
    opacity: 0;
  }

  .browseButton {
    position: absolute;
    text-align: center;
    right: 10px;
    font-size: 30px;
    height: 47px;
    top: 28vh;
    width: 60%;
    z-index: -2000;
    opacity: 0;
  }

  .homeButtonActive {
    z-index: 20000;
    position: absolute;
    right: 10px;
    text-align: center;
    font-size: 25px;
    height: 47px;
    top: 12vh;
    width: 60%;
    animation: 0.5s homeLinkFade;
  }

  .browseButtonActive {
    position: absolute;
    text-align: center;
    right: 10px;
    font-size: 25px;
    height: 47px;
    top: 18vh;
    width: 60%;
    z-index: 20000;
    animation: 0.5s browseLinkFade;
  }

  .compareButton {
    z-index: 200;
    position: absolute;
    right: 10px;
    text-align: center;
    font-size: 30px;
    height: 47px;
    top: 15vh;
    width: 60%;
    z-index: -2000;
    opacity: 0;
  }

  .worldButton {
    position: absolute;
    text-align: center;
    right: 10px;
    font-size: 30px;
    height: 47px;
    top: 28vh;
    width: 60%;
    z-index: -2000;
    opacity: 0;
  }

  .compareButtonActive {
    z-index: 20000;
    position: absolute;
    right: 10px;
    text-align: center;
    font-size: 25px;
    height: 47px;
    top: 25vh;
    width: 60%;
    animation: 0.5s homeLinkFade;
  }

  .worldButtonActive {
    position: absolute;
    text-align: center;
    right: 10px;
    font-size: 25px;
    height: 47px;
    top: 32vh;
    width: 60%;
    z-index: 20000;
    animation: 0.5s browseLinkFade;
  }

  .burger {
    z-index: 1000;
    display: block;
    opacity: 100;
  }
}

