/* Compare Stats Css */ 

@keyframes expandBar {
  from { width: 0; } to { width:var(--bar-value); }
}

.stat-bars-label p{
  margin: 0%;
  font-family: "Arvo", sans-serif;
  font-size: 0.8em;
  font-weight: bold;
}

.comparison-bar1 {
  width: var(--bar-value);
  background-color: #F2726F;
  color: rgb(0, 0, 0);
  animation-name: expandBar;
  animation-duration: 1s;
  border-radius: 5px;
  font-size: 0.8em;
  font-family: "Arvo", sans-serif;
}

.comparison-bar2 {
  width: var(--bar-value);
  background-color: #5D62B5;
  color: rgb(0, 0, 0);
  animation-name: expandBar;
  animation-duration: 1s;
  border-radius: 8px;
  font-size: 0.8em;
  font-family: "Arvo", sans-serif;
}

